import { h, render, Component } from 'preact';
import { connect } from 'preact-redux';
import store from '../store';

class Enter extends Component {
  constructor() {
    super();

    this.start = this.start.bind(this);
  }

  start() {
    store.dispatch({
      type: 'TEST_STARTED',
      isStarted: true
    });

    this.props.test.slideTo(0, this.props.test.height, (offsetY) => {
      store.dispatch({
        type: 'TEST_SLIDE',
        offsetY: offsetY
      });
    });
  }

  render(props, state) {
    return (
      <div className="mailru-enter">
        <div className="mailru-enter__title"> </div>
        <div className="mailru-enter__text">
          <p>15  2018  MRG    20 .             ,   .</p>
          <p>       ,      .</p>
        </div>
        <div className="mailru-enter__start">
          <button className="mailru-enter__start-btn" onClick={this.start}></button>
          <div className="mailru-enter__start-line"></div>
        </div>
      </div>
    )
  }
};

const mapStateToProps = function(store) {
  return {
    test: store.testState
  };
};

export default connect(mapStateToProps)(Enter);